import type { FC } from 'react';

import { Carousel } from 'react-responsive-carousel';
import 'react-responsive-carousel/lib/styles/carousel.min.css';

import { ENAV } from '../../../../../shared/components/Navigation/interface/interface';

import '../styles/style.css';

interface IAdvantagesProps {
  windowWidth: number;
}

const Advantages: FC<IAdvantagesProps> = ({ windowWidth }) => {

  console.log(windowWidth);

  return (
    <div className='advantages' id={ENAV.ADVANTAGES}>
      <div className='advantages__container'>
        <h2 className='advantages__title'> </h2>
        <Carousel
          swipeable
          autoPlay={true}
          showThumbs={false}
          showStatus={false}
          showIndicators={false}
          showArrows={false}
          infiniteLoop
          centerMode
          centerSlidePercentage={30}
        >
          <div className='advantages__item'>
            <div className='advantages__item-icon'></div>
            <h4 className='advantages__item-title'> </h4>
            <h4 className='advantages__item-text'>    &nbsp; &nbsp;  </h4>
          </div>
          <div className='advantages__item'>
            <div className='advantages__item-icon'></div>
            <h4 className='advantages__item-title'> &nbsp; </h4>
            <h4 className='advantages__item-text'>       &nbsp; &nbsp; </h4>
          </div>
          <div className='advantages__item'>
            <div className='advantages__item-icon'></div>
            <h4 className='advantages__item-title'>  &nbsp; </h4>
            <h4 className='advantages__item-text'>  &nbsp;-,  &nbsp; &nbsp;     </h4>
          </div>
          <div className='advantages__item'>
            <div className='advantages__item-icon'></div>
            <h4 className='advantages__item-title'> </h4>
            <h4 className='advantages__item-text'>     </h4>
          </div>
          <div className='advantages__item'>
            <div className='advantages__item-icon'></div>
            <h4 className='advantages__item-title'> </h4>
            <h4 className='advantages__item-text'> &nbsp;   &nbsp;   2025 &nbsp;</h4>
          </div>
        </Carousel>
      </div>
    </div>
  );
};

export default Advantages;
