import type { FC } from 'react';

import { Link } from 'react-scroll';

import { ENAV } from '../../../../../shared/components/Navigation/interface/interface';

import '../styles/style.css';

interface IDescriptionProps {
  windowWidth: number;
}

const Description: FC<IDescriptionProps> = ({ windowWidth }) => {

  console.log(windowWidth);

  return (
    <div className='description' id={ENAV.DESCRIPTION}>
      <span className='description__subtitle'> </span>
      <h2 className='description__title'>&nbsp;&nbsp; &nbsp;?</h2>
      <div className='description__container'>
        <div className='description__img'></div>
        <p className='description__text'>    ,        .</p>
      </div>
      <div className='description__info'>
        <p className='description__info-text'>  ,   :  ,   ,   ,        .</p>
        <p className='description__info-text'>        .</p>
        <p className='description__info-link'><Link to={ENAV.RECRUITMENT} smooth={true} offset={0} duration={500} spy={true}></Link></p>
      </div>
    </div>
  );
};

export default Description;
