import type { FC } from 'react';

import { useNavigate } from 'react-router-dom';

import Button from '../../../../../shared/components/Button/ui/Button';
import CountdownTimer from '../../../../../widgets/CountdownTimer/ui/CountdownTimer';

import { EROUTES } from '../../../../../shared/utils/ERoutes';
import { registrationOverDate } from '../../../../../shared/mock/dates';

import '../styles/style.css';

interface IMainProps {
  windowWidth: number;
}

const Main: FC<IMainProps> = ({ windowWidth }) => {

  const navigate = useNavigate();

  const openLoginPage = () => {
    navigate(EROUTES.LOGIN);
  };

  const openRegistrationPage = () => {
    navigate(EROUTES.REGISTRATION);
  };
  
  return (
    <main className='main' id='main'>
      {
        windowWidth > 1000
        ?
        <>
          <span className='main__caption'>&nbsp; &nbsp;&nbsp;</span>
          <div className='main__title-container'>
            <h1 className='main__title'> </h1>
            <span className='main__title-stroke'></span>
            <h1 className='main__title main__title_color_transparent'> </h1>
          </div>
          <div className='main__info'>
            <p className='main__subtitle'>  &nbsp;     &nbsp;  &nbsp;2035  &nbsp; &nbsp;,  &nbsp;   &nbsp;  .</p>
            <div className='main__time'>
              <p className='main__timer-text'>   </p>
              <div className='main__timer'>
                <CountdownTimer targetDate={registrationOverDate} />
              </div>
            </div>
          </div>
          <div className='main__buttons'>
            <Button text='' onClick={openRegistrationPage} />
            <Button text=' ' onClick={openLoginPage} color='inherit' />
          </div>
        </>
        :
        <> 
        </>
      }
    </main>
  );
};

export default Main;
